@import url(https://fonts.googleapis.com/css?family=Comfortaa&display=swap);
* {
    font-family:  'Comfortaa', cursive;
}
.page-container {
    margin-top: 20px;
}

.color-confirmed {
    color: #FF073A;
}

.color-active {
    color: #007BFF;
}

.color-recovered {
    color: #28A745;
}

.color-deceased {
    color: #6C757D;
}

.bg-color-confirmed {
    background-color: #FEE1E1;
}

.bg-color-active {
    background-color: #F3F4FF;
}

.bg-color-recovered {
    background-color: #E1F2E8;
}

.bg-color-deceased {
    background-color: #F6F6F7;
}
.totals-card {
    text-align: center;
}
.totals-card-title {
    font-size: 14px;
    font-weight: 600;
}

.totals-card-count {
    font-size: 18px;
    font-weight: 600;
}

.totals-card-table {
    margin-top: 2em;
}

@media (max-width: 575px) {
    .totals-card-container {
        width: auto;
    }

    .totals-card-table {
        display: flex;
        align-items: stretch;
    }
}

@media (min-width: 385px) and (max-width: 575px){
    .totals-card-title {
        font-size: 0.7rem;
    }
    .totals-card-count {
        font-size: 0.9rem;
    }
}

@media (min-width: 348px) and (max-width: 384px) {
    .totals-card-title {
        font-size: 0.6rem;
    }
    .totals-card-count {
        font-size: 0.8rem;
    }
}

@media (max-width: 347px) {
    .totals-card-title {
        font-size: 0.5rem;
    }
    .totals-card-count {
        font-size: 0.7rem;
    }
}
.country-table-cell {
    font-size: 0.7rem;
}

.country-table-header {
    font-size: 0.8rem;
}

@media (max-width: 575px) {
    .country-table-cell {
        font-size: 0.6rem;
    }

    .country-table-header {
        font-size: 0.7rem;
    }
}
.country-table-title {
    text-align: center;
    font-size: 1.1rem;
    font-weight: 500;
    padding: 10px;
}

.affected-countries-count {
    font-size: 0.7rem;
    font-weight: 600;
    text-transform: uppercase;
    padding: 0;
    margin: 0;
}

.country-table-wrapper {
    height: 350px;
    overflow-y: scroll;
    padding: 0.3em;
}

.country-table-header {
    font-weight: 600;
}

@media (max-width: 575px) {
    .affected-countries-count {
        font-size: 0.6rem;
    }

    .country-table-header {
        font-size: 0.6rem;
    }
    
    .rdt_TableCell, .rdt_TableCol {
        min-width: 0px !important;
        max-width: 20% !important;
        font-size: 0.5rem !important;
    }
}
.last-updated-header {
    color: #008000;
    font-weight: 600;
    font-size: 0.6rem;
}

.last-updated-time {
    color: #008000;
    font-weight: 600;
    font-size: 0.7rem;
    text-transform: uppercase;
}

@media (max-width: 575px) {
    .dashboard-header-col.title {
        word-wrap: break-word;
        max-width: 60%;
    }

    .dashboard-header-col.time {
        word-wrap: break-word;
        max-width: 40%;
    }
}
.country-stats-card {
    padding: 0.2rem;
    border-radius: 5px;
    margin-bottom: 0.4rem;
    text-align: center;
}
.country-stats-card-title {
    font-size: 0.7rem;
    font-weight: 700;
    padding: 0;
    margin: 0;
}

.country-stats-card-count {
    font-size: 0.8rem;
    font-weight: 700;
    padding: 0;
    margin: 0;
}
.stats-card-country-name {
    font-weight: 700;
    font-size: 0.8rem;
}

.country-stats {
    margin-top: 0.7em;
}


@media (max-width: 575px) {
    .country-stats-card-wrapper {
        width: auto;
    }
}

@media (min-width: 385px) and (max-width: 575px){
    .country-stats-card-title {
        font-size: 0.7rem;
    }
    .country-stats-card-count {
        font-size: 0.9rem;
    }
    .country-stats-card-wrapper {
        padding: 0.6em;
    }
}

@media (min-width: 348px) and (max-width: 384px) {
    .country-stats-card-title {
        font-size: 0.6rem;
    }
    .country-stats-card-count {
        font-size: 0.8rem;
    }

    .country-stats-card-wrapper {
        padding: 0.6em;
    }
}

@media (max-width: 347px) {
    .country-stats-card-title {
        font-size: 0.6rem;
    }
    .country-stats-card-count {
        font-size: 0.7rem;
    }
    .country-stats-card-wrapper {
        padding: 0.4em;
    }
}
.world-map {
    height: 350px;
}

.map-container {
    margin-top: 0.6em;
}

@media (max-width: 991px) {
    .world-map-header {
        margin-top: 15px;
    }
}
.footer-container {
    text-align: center;
    margin-top: 30px;
}
